import React, {Component} from 'react';
import Cell from './Cell';

export default class Grid extends Component{

    constructor(props){
        super(props);

        this.state = {
            grid: props.gridArray,
        };

    }


    makeGrid(){
        let grid = [];
        let key = 0;

        for(let i = 0;i < this.state.grid.length;i++){
            for(let j = 0;j < this.state.grid[0].length;j++){
                key++;
                grid.push(
                    <Cell key = {key} serialNumber = {this.state.grid[i][j]}/>
                );
            }
        }


        return grid;
    }

    render(){
        let grid = this.makeGrid();
        return(
            <div className="grid">
                {
                   grid
                }
            </div>
        );
    }
}